import logo from './logo.svg';
import './App.css';
import mera from '../src/images/mera.PNG';
import carousel from '../src/images/carousel.JPG';
import resume from '../src/images/resume.PNG';
import periodicTable from '../src/images/periodicTable.PNG';
import port from '../src/images/port.PNG';
import racegame from '../src/images/racegame.PNG';
import sidebar from '../src/images/sidebar.PNG';
import salesforce from '../src/images/salesforce.png';
import { useState, useEffect } from 'react';

const images = [
  {
    id:1, ImgName: mera, tag: 'CSS'
  },
  {
    id:2, ImgName: carousel, tag: 'React'
  },
  {
    id:3, ImgName: resume, tag: 'React'
  },
  {
    id:4, ImgName:periodicTable, tag: 'HTML'
  },
  {
    id:5, ImgName: port, tag: 'Bootstrap'
  },
  {
    id:6, ImgName: racegame, tag: 'JavaScript'
  },
  {
    id:7, ImgName: sidebar, tag: 'CSS'
  },
  {
    id:8, ImgName: salesforce, tag: 'CSS'
  }
]
function App() {
   const [tag, setTag ] = useState();
   const [filteredImages, setFilteredImages] = useState([])

   useEffect(() => {
    tag === 'All' ? setFilteredImages(images) : setFilteredImages(images.filter((img) => {return(img.tag ===tag)}))
   }, [tag])
   
  return (
    
    <div>
    <nav>
      <ul>
      <li onClick={()=>setTag('All')} > All</li>

        <li onClick={()=>setTag('HTML')} > HTML</li>
        <li  onClick={()=>setTag('CSS')}>CSS</li>
        <li onClick={()=>setTag('React')}>React</li>
        <li onClick={()=>setTag('Bootstrap')}>Bootstrap</li>
        <li onClick={()=>setTag('JavaScript')}>JavaScript</li>
      </ul>
    </nav>
      <div className="App">
    
      {
        filteredImages.map((img)=>{
         return( <div>
            { 
              <img src={img.ImgName} alt="" srcset="" />
            }
          </div>)
        })
      }
    </div>
    </div>
  );
}

export default App;
